<template lang="html">
   <div>
      <h3></h3>
      <a href="add.jsp"></a><br/>
      <table border="1">
      <tr><th></th><th></th><th></th><th></th><th></th><th></th></tr>
         <tr v-for="item in list">
            <td>{{item.id}}</td><td>{{item.name}}</td>
              <td>{{item.sex}}</td><td>{{item.age}}</td><td>{{item.phone}}</td>
              <td>
              <router-link
                :to="{name:'updateEmloyee',params:{id:item.id}}"></router-link>
              <a style="cursor: pointer;" onclick="deleteL(1)">
              </a>
            </td>
         </tr>
      </table>
    </div>

</template>

<script>
export default {
  name: 'listEmloyee',
  data(){
    return{
      list:[]
    }
  },
  created(){
    this.$http.get(
        '/employeesys_ssh/list',
        ).then((res)=>{
          this.list = res.body.msg;
        console.log(res)
      })
  },
  mounted:function(){
    
  }
}
</script>

<style lang="css">
  table td {
        text-align: center;
      }
      /**/
      
      
      
      h3 {
        text-align: center;
      }
      
      
</style>



